             src=\"/assets/manager_tools/sec9img.png\"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div\n            id=\"section-3\"\n            class=\"container-fluid position-relative\"\n            style=\"margin: 30px 0\"\n        >\n            <div class=\"container-lg\">\n                <div class=\"row\" id=\"rev\">\n                    <div class=\"col-sm-6\">\n                        <img\n                            style=\"width: 100%\"\n                            src=\"/assets/manager_tools/sec10img.png\"\n                            alt=\"\"\n                        />\n                    </div>\n                    <div class=\"col-sm-6\">\n                        <div class=\"section-3-content\">\n                            <!-- <h3>GET MORE DONE</h3> -->\n                            <h1>Manage wedding events</h1>\n                            <h1>including vendors, timelines,</h1>\n                            <h1>and guest transportation.</h1>\n                            <p>\n                                From transportation options for guests to\n                                coordinating the timing of events throughout the\n                                day, our platform makes wedding planning a\n                                breeze. Discover how easy it can be to execute\n                                your dream wedding with Happywed's logistics\n                                tools.\"\n                            </p>\n                            <!-- <a :href=\"links.invitations\">Create invitation</a> -->\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div id=\"section-3\" class=\"container-fluid position-relative\">\n            <div class=\"container-lg\">\n                <div class=\"row\">\n                    <div class=\"col-sm-6\">\n                        <div class=\"section-3-content\">\n                            <!-- <h3>GET MORE DONE</h3> -->\n                            <h1 style=\"text-align: left\">\n                                \"Stay on track with Tasklist:\n                            </h1>\n                            <h1 style=\"text-align: left\">\n                                Add, manage, and complete\n                            </h1>\n                            <h1 style=\"text-align: left\">\n                                wedding planning tasks easily.\"\n                            </h1>\n                            <p style=\"text-align: left\">\n                                The \"Tasklist\" tool helps you stay organized and\n                                on top of your wedding planning tasks. You can\n                                create a to-do list and set deadlines for each\n                                task, making sure nothing falls through the\n                                cracks. The tool also allows them to assign\n                                tasks to different members of their wedding\n                                party or vendors, keeping everyone on the same\n                                page.\n                            </p>\n                            <!-- <a :href=\"links.invitations\">Create invitation</a> -->\n                        </div>\n                    </div>\n                    <div class=\"col-sm-6\" style=\"text-align: end\">\n                        <img\n                            style=\"width: 80%\"\n                            src=\"/assets/manager_tools/sec11img.svg\"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div class=\"new-revi-cont\">\n            <div class=\"pink-bar\"></div>\n            <h1 class=\"top-heading\">Testimonials of what our clients thinks</h1>\n      