                                    alt=\"\"\n                                        />\n                                    <div class=\"test-name\">Michael Knight</div>\n                                    <div class=\"test-review\">{{item.body}}</div>\n                                    <div class=\"star-cont\">\n                                        <img v-for=\"(ele,indexx) in Number(item.stars)\" :key=\"indexx\"  src=\"/assets/ph_star-fill.svg\" alt=\"\">\n                                        <img v-for=\"(ele,indexx) in (5-item.stars)\" src=\"/assets/ph_star-light.svg\" alt=\"\">\n                                    </div>\n                                </div>\n                            </li>\n                        </ul>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <!-- <div id=\"section-8\">\n            <div style=\"width: max-content; margin: auto; overflow-x: auto\">\n                <div\n                    v-for=\"(item, index) in priceCards\"\n                    :key=\"index\"\n                    class=\"price-card-container\"\n                >\n                    <div>\n                        <div :style=\"item.color\" class=\"plan\">\n                            {{ item.type }}\n                        </div>\n                    </div>\n                    <div class=\"details-cont\">\n                        <div>/Per Month</div>\n                        <div style=\"text-align: center\">\n                            <span>â¹</span><span>{{ item.price }}</span>\n                        </div>\n                        <div>\n                            \"Lorem ipsum dolor sit hjbsdc jsnznxk\n                            amet,consectetur adipiscing elit, sed do eiusmod\n                            tempor incididunt ut labore et dolore magna aliqua.\n                            Ut enim ad minim veniam, quis nostru exercitation.\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector3.svg\" alt=\"\" />\n                            <span\n                                >illo inventore veritatis et quasi architecto\n                                beatae vitae</span\n                            >\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector3.svg\" alt=\"\" />\n                            <span\n                                >quis nostrum exercitationem ullam corporis\n                                suscipit laboriosam, nisi ut aliquid ex ea\n                                commodi consequatur? Quis autem vel</span\n                            >\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector4.svg\" alt=\"\" />\n                            <span\n                                >Duis aute irure dolor in reprehenderit in\n                                voluptate velit esse cillum dolore eu\n                            </span>\n                        </div>\n                        <div class=\"pros\">\n                            <img src=\"/assets/Vector4.svg\" alt=\"\" />\n                            <span\n                                >eius modi tempora incidunt ut labore et dolore\n                                magnam aliquam quaerat voluptatem.</span\n                            >\n                        </div>\n                        <div style=\"text-align: center; margin: 1em 0\">\n                            <button class=\"buy\">Buy Now</button>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div> -->\n    </div>\n</template>\n\n<script>\nimport Footer from \"./Footer.vue\";\nimport Header from \"./Header.vue\";\nexport default {\n    props: [\"testimonials\"],\n    data() {\n        return {\n            cont: null,\n            links: {\n                invitation: \"/host/invitations\",\n                saveDate: \"/host/savedate\",\n                story: \"/host/story\",\n                ceramony: \"/host/ceramony\",\n                accomodation: \"/host/accommodation\",\n                guestlist: \"/host/invitedguests\",\n                logistics: \"/host/logistics\",\n            },\n        };\n    },\n    components: {\n        Footer,\n        Header,\n    },\n    methods: {\n        testimonialSplide() {\n            let _this = this;\n            _this.cont = new Splide(`#landing-page-testimonial`, {\n                type: \"loop\",\n                // clones: 0,\n                gap: 5,\n                rewind: true,\n                pagination: true,\n                cover: true,\n                isNavigation: true,\n                arrows: true,\n                gap: 10,\n                // perPage: 3,\n                autoWidth: true,\n                padding: \"20%\",\n                focus: \"center\",\n                breakpoints: {\n                    968: {\n                        perPage: 2,\n                    },\n                    768: {\n                        perPage: 1,\n                        autoWidth: false,\n                        padding: \"10px\",\n                    },\n                    567: {\n                        padding: \"0px\",\n                        arrows: false,\n                        wheel: true,\n                        direction: \"ltr\",\n                        releaseWheel: false,\n                        pagination: false,\n                    },\n                },\n            });\n            _this.cont.mount();\n        },\n        getInviUser() {\n            let _this = this;\n            console.log(\"ge\");\n            axios\n                .get(\"/host/host-invi\")\n                .then(function (response) {\n                    if (response.data.invi) {\n                        console.log(re