A,gBAAA,CAMA,iBAAA,CACA,iBAAA,CACA,QAAA,CALA,WAMA,CACA,+BACA,eAAA,CACA,kBAAA,CAEA,qBAAA,CACA,WACA,CACA,+CAMA,aAAA,CALA,iBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAGA,iBAAA,CADA,gBAEA,CACA,iEAMA,UAAA,CALA,iBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAEA,kBACA,CAEA,kEAMA,UAAA,CALA,iBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAEA,CACA,gDAMA,UAAA,CAFA,cAAA,CACA,gBAAA,CAGA,mBAAA,CADA,cAEA,CACA,uEATA,wBAAA,CACA,iBAAA,CACA,eAgBA,CATA,uBAMA,aAAA,CACA,YAAA,CAHA,aAAA,CACA,eAAA,CAGA,kBACA,CACA,2BAGA,WAAA,CAFA,iBAAA,CACA,UAEA,CACA,4BACA,qBACA,CACA,sBASA,iDAAA,CAFA,WAAA,CAGA,kBAAA,CAJA,UAAA,CALA,wBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAGA,SAAA,CAGA,iBACA,CAEA,gCACA,kBAAA,CACA,aACA,CACA,2BAGA,kBAAA,CADA,iBAAA,CAGA,UAAA,CAJA,WAAA,CAGA,WAEA,CACA,mCACA,UAAA,CAEA,mBAAA,CACA,iBAAA,CACA,iBAAA,CACA,eAAA,CACA,kBAAA,CALA,iBAMA,CACA,6CAEA,iBAAA,CADA,eAEA,CACA,sCACA,gBACA,CACA,iCAEA,eAAA,CADA,kBAAA,CAEA,0CAAA,CAKA,WAAA,CAFA,eAAA,CAFA,YAAA,CACA,iBAAA,CAEA,iBAEA,CACA,qCACA,kBAAA,CAEA,WAAA,CAEA,qBAAA,CAHA,UAIA,CACA,4BAGA,cAAA,CAEA,eAAA,CAEA,kBACA,CACA,0DARA,UAAA,CACA,mBAAA,CAEA,iBAAA,CAEA,kBAeA,CAZA,8BAIA,cAAA,CAEA,eAAA,CAEA,kBAAA,CACA,gBAAA,CACA,eAAA,CACA,oBAAA,CATA,iBAUA,CAEA,iDACA,wBAAA,CACA,UACA,CAIA,oHACA,qBACA,CACA,uDACA,wBAAA,CAEA,qBAAA,CADA,kBAEA,CACA,6DACA,wBAAA,CACA,qBACA","sourcesContent":["<template>\n    <div>\n        <div id=\"section-1\" class=\"container-lg position-relative\">\n            <div class=\"manager-section-1-head\">\n                <h3>Smart tools to Mange your wedding</h3>\n                <h1>Wedding Managing Tools</h1>\n                <p>\n                    Manage your dream wedding with ease using our Wedding\n                    Manager Tools. From creating beautiful invitations to\n                    booking accommodations for your guests, you can easily\n                    manage every aspect of your wedding.\n                </p>\n                <!-- <a :href=\"links.invitations\">Create invitation</a> -->\n            </div>\n            <div class=\"manager-section-1-images\">\n                <div\n                    style=\"\n                        width: 50%;\n                        position: absolute;\n                        bottom: 0;\n                        right: 0;\n                        height: 100%;\n                    \"\n                >\n                    <img src=\"/assets/manager_tools/Ellipse.png\" />\n                    <img src=\"/assets/manager_tools/first_pic.png\" />\n                    <img src=\"/assets/manager_tools/heart_design.svg\" />\n                </div>\n            </div>\n        </div>\n        <div id=\"section-2\" class=\"container-fluid position-relative\">\n            <h2>Managerial tools</h2>\n            <h3>\n                Creating moments to last a lifetime.Provide every service\n                related to the function of marriage\n            </h3>\n            <div>\n                <a :href=\"links.invitation\">\n                    <img src=\"/assets/manager_tools/4.png\" alt=\"\" />\n                    <p>Invitation</p>\n                    <p>Customise your invitation</p>\n                </a>\n                <a :href=\"links.saveDate\">\n                    <img src=\"/assets/manager_tools/8.png\" alt=\"\" />\n                    <p>Save the date</p>\n                    <p>Create cuntdown for your special day</p>\n                </a>\n                <a :href=\"links.story\">\n                    <img src=\"/assets/manager_tools/9.png\" alt=\"\" />\n                    <p>Story</p>\n                    <p>Share your timeline love story with us</p>\n                </a>\n                <a :href=\"links.ceramony\">\n                    <img src=\"/assets/manager_tools/5.png\" alt=\"\" />\n                    <p>Ceremonies</p>\n                    <p>Organise your ceremony dates</p>\n                </a>\n                <a :href=\"links.accomodation\">\n                    <img src=\"/assets/manager_tools/6.png\" alt=\"\" />\n                    <p>Accomodation</p>\n                    <p>Book hotel rooms for your guests</p>\n                </a>\n                <a :href=\"links.guestlist\">\n                    <img src=\"/assets/manager_tools/2.png\" alt=\"\" />\n                    <p>Guestlist</p>\n                    <p>Upload your wedding guestlist</p>\n                </a>\n                <a :href=\"links.logistics\">\n                    <img src=\"/assets/manager_tools/7.png\" alt=\"\" />\n                    <p>Logistics</p>\n                    <p>Book logistics to make it simple</p>\n                </a>\n                <a>\n                    <img src=\"/assets/manager_tools/3.png\" alt=\"\" />\n                    <p>Tasklist</p>\n                    <p>Create your own to-do list</p>\n                </a>\n            </div>\n        </div>\n        <div id=\"section-3\" class=\"container-fluid position-relative\">\n            <h2>Design Your Wedding Invitation</h2>\n            <h3>\n                Design beautiful and customized wedding invitations that\n                perfectly match the theme of their special day.\n            </h3>\n            <div class=\"container-lg\">\n                <div class=\"row\">\n                    <div class=\"col-sm-6\">\n                        <img\n                            style=\"width: 80%\"\n                            src=\"/assets/manager_tools/section-2img.png\"\n                            alt=\"\"\n                        />\n                    </div>\n                    <div class=\"col-sm-6\">\n                        <div class=\"section-3-content\">\n                            <!-- <h3>GET MORE DONE</h3> -->\n                            <h1>Create</h1>\n                            <h1>Organize your Wedding</h1>\n                            <h1>Complete more task every day.</h1>\n\n                            <p>\n                                Save the date for your wedding celebration!\n                                We're excited to announce that we're tying the\n                                knot, and we'd love for you to be a part of our\n                                special day. Let's start the countdown for your\n                                wedding day.\n                            </p>\n                            <!-- <a :href=\"links.invitations\">Create invitation</a> -->\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div id=\"section-3A\" class=\"container-fluid position-relative\">\n            <!-- <h2>Design Your Wedding Invitation</h2>\n            <h3>\n                Design beautiful and customized wedding invitations that\n                perfectly match the theme of their special day.\n            </h3> -->\n            <div class=\"container-lg\">\n                <div class=\"row\">\n                    <div\n                        class=\"col-sm-6\"\n                        style=\"\n                            display: flex;\n                            justify-content: center;\n                            align-items: center;\n                        \"\n                    >\n                        <div class=\"section-3-content\">\n                            <!-- <h3>GET MORE DONE</h3> -->\n                            <h1 style=\"text-align: left\">\n                                Make a personalized countdown for your wedding\n                                day.\n                            </h1>\n                            <p style=\"text-align: left\">\n                                Save the date for your wedding celebration!\n                                We're excited to announce that we're tying the\n                                knot, and we'd love for you to be a part of our\n                                special day. Let's start the countdown for your\n                                wedding day.\n                            </p>\n                            <!-- <a :href=\"links.invitations\">Create invitation</a> -->\n                        </div>\n                    </div>\n                    <div\n                        class=\"col-sm-6\"\n                        style=\"\n                            display: flex;\n                            justify-content: center;\n                            align-items: center;\n                        \"\n                    >\n                        <img\n                            style=\"width: 100%; max-width: 450px\"\n                            src=\"/assets/manager_tools/Group_1114.svg\"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div id=\"section-4\" class=\"container-fluid position-relative\">\n            <h2>Our Story</h2>\n            <h3>\n                Share your love story with Happywed and inspire others. From the\n                first meeting to the wedding day, tell us how you fell in love\n                and planned your dream celebration. Start creating your own love\n                story today with Happywed.\n            </h3>\n            <div class=\"container-lg\">\n                <div class=\"section-4-content\">\n                    <div>\n                        <img src=\"/assets/manager_tools/12.svg\" alt=\"\" />\n                        <h3>2019</h3>\n                        <h4>How we met</h4>\n                    </div>\n                    <div>\n                        <img src=\"/assets/manager_tools/13.svg\" alt=\"\" />\n                        <h3>2020</h3>\n                        <h4>First meet</h4>\n                    </div>\n                    <div>\n                        <img src=\"/assets/manager_tools/11.svg\" alt=\"\" />\n                        <h3>2021</h3>\n                        <h4>the day we exchanged hearts</h4>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div id=\"section-5\" class=\"container-fluid position-relative\">\n            <div class=\"container-lg\">\n                <div class=\"row\">\n                    <div class=\"col-sm-6\">\n                        <img\n                            style=\"width: 100%\"\n                            src=\"/assets/manager_tools/sec-5img.png\"\n                            alt=\"\"\n                        />\n                        <!-- <a :href=\"links.invitations\">Create ceremonies</a> -->\n                    </div>\n                    <div\n                        class=\"col-sm-6\"\n                        style=\"\n                            display: flex;\n                            flex-direction: column;\n                            justify-content: center;\n                        \"\n                    >\n                        <h1 style=\"text-align: right\">\n                            Create and Manage your wedding ceremonies\n                        </h1>\n                        <p style=\"text-align: right\">\n                            You can create your wedding ceremonies by using our\n                            customizable templates that suit your preferences\n                            and style. This can include selecting the right\n                            flowers and decorations, designing the perfect\n                            wedding arch, and crafting personalized vows to\n                            exchange with their partner which makes to\n                            ceremonies days rememberable.\n                        </p>\n                        <!-- <a :href=\"links.invitations\">Create ceremonies</a> -->\n                    </div>\n                </div>\n            </div>\n        </div>\n        <!-- <div id=\"section-6\" class=\"container-fluid position-relative\">\n            <div style=\"width: 60%\">\n                <h1>Add your tasks.</h1>\n   