gn: left\">\n                                Book Hotels for your guests.\n                            </h1>\n                            <p style=\"text-align: left\">\n                                Happywed offers a wide range of accommodation\n                                options for your wedding guests, from luxurious\n                                hotels to cozy bed and breakfasts. Our platform\n                                makes it easy to find the perfect place to stay\n                                and ensures your guests have a comfortable and\n                                enjoyable experience.\n                            </p>\n                            <!-- <a :href=\"links.invitations\">Create invitation</a> -->\n                        </div>\n                    </div>\n                    <div class=\"col-sm-6\">\n                        <img\n                            style=\"width: 100%\"\n                            src=\"/assets/manager_tools/sec9img.png\"\n                            alt=\"\"\n                        />\n                    </div>\n                </div>\n            </div>\n        </div>\n        <div\n            id=\"section-3\"\n            class=\"container-fluid position-relative\"\n            style=\"margin: 30px 0\"\n        >\n            <div class=\"container-lg\">\n                <div class=\"row\" id=\"rev\">\n                    <div class=\"col-sm-6\">\n                        <img\n                            style=\"width: 100%\"\n                            src=\"/assets/manager_tools/sec10img.png\"\n                            alt=\"\"\n                        />\n                    </div>\n                    <div class=\"col-sm-6\">\n                        <div class=\"section-3-content\">\n                            <!-- <h3>GET MORE DONE</h3> -->\n                            <h1>Manage wedding events</h1>\n                            <h1>including vendors, timelines,</h1>\n         